.save-btn {
  background-color: #faa805;
  border: none;
  padding: 12px 24px;
  cursor: pointer;
  font-size: 1em;
  color: white;
  border-radius: 5px;
}

.profile-fields {
  flex: 0 0 47.5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  right: 0;
}

.profile-field {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 600;
}

.field-label {
  color: grey;
}

/* Add this to your existing CSS or create a new CSS file */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /* Ensure it appears above other elements */
}

.popup-content {
  background: #fff; /* Popup background color */
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  position: relative;
}

.popup-text {
  color: black;
  font-size: 20px;
  font-weight: 600;
}

.popup-btn {
  background-color: #faa805;
  color: black;
  padding: 7px 15px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px;
}

.close-popup {
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
  outline: none;
  color: black;
  font-weight: 900;
}

.delete-button {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  padding: 5px 8px;
  cursor: pointer;
}

.delete-button .x-button {
  color: white;
  font-size: 25px;
}

.add-button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border: 4px solid white;
  border-radius: 50%;
  color: white;
  font-size: 25px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 10px;
}

@media (max-width: 768px) {
  .my-account-page {
    flex-direction: column;
    height: calc(100vh - 150px);
    display: block;
  }

  .profile-fields {
    position: relative;
    height: unset;
    margin-top: 40px;
  }

  .profile-pics {
    height: unset;
  }
}
